.product-details-link {
    text-decoration: none;
    transition: 0.4s;
    /* width: 23%; */
}
.product-details-link .each-product-container{
    transition: 0.3s;
    height: 100%;
    display: flex;
    flex-direction: column;
}
.product-details-link .each-product-container img{
    max-width: 80%;
    max-height: 90%;
}
.products-container .each-product-container .card-body{
    display: flex;
    flex-direction: column-reverse;
}
.product-image-wishlist-button-container .image-container {
    width: 100%;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.wishlist-button-container{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.product-add-wishlist-icon{
    display: flex;
    justify-content: end;
    margin-right: 15px;
    margin-top: 10px;
}



@keyframes wishlist-icon{
    0%{
        width: 20%;
        height: 20%;
        opacity: 0.5;
    }
    100%{
        width: 100%;
        height: 100%;
        opacity: 1;
    }
}
.product-add-wishlist-icon svg{
    color: rgb(174, 172, 172);
    width: 100%;
    height: 100%;
    animation: wishlist-icon 0.4s linear;
}
.product-add-wishlist-icon span{
    width: 100%;
    height: 100%;
    animation: wishlist-icon 0.4s linear;
}


@keyframes wishlist-loading{
    0%{
        width: 20%;
        height: 20%;
        opacity: 1;
        background-color: green;
    }
    100%{
        width: 100%;
        height: 100%;
        opacity: 0;
        background-color: green;
    }
}
.wishlist-action-loader{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    animation: wishlist-loading 0.6s linear infinite;
}



/* media queries starts */
@media (min-width:1200px){
    .product-details-link {
        width: 23%;
    }
}

@media (width > 992px) and (width < 1200px){
    .product-details-link {
        width: 30%;
    }
    .product-image-wishlist-button-container .image-container {
        height: 270px;
    }
}

@media (width >= 768px) and (width <=992px){
    .product-details-link {
        width: 40%;
    }
    .product-image-wishlist-button-container .image-container {
        height: 270px;
    }
}

@media (width < 768px){
    .product-details-link{
        width: 100%;
    }
    .product-details-link .each-product-container {
        padding: 5px 5px 15px 15px;
    }
}